  src: url("../fonts/Raleway-SemiBold.woff2") format("woff2"), url("../fonts/Raleway-SemiBold.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Raleway-Regular";
  src: url("../fonts/Raleway-Regular.woff2") format("woff2"), url("../fonts/Raleway-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Nunito-Regular";
  src: url("../fonts/Nunito-Regular.woff2") format("woff2"), url("../fonts/Nunito-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Raleway-Thin";
  src: url("../fonts/Raleway-Thin.woff2") format("woff2"), url("../fonts/Raleway-Thin.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

body {
  font-size: 15px;
  color: #fff;
  letter-spacing: 0.02em;
  margin: 0;
  background-color: #242424;
  font-family: Raleway;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
  text-transform: uppercase;
  margin: 0;
}

textarea {
  resize: none;
}

h1 {
  font-size: 42px;
}

h2 {
  font-size: 30px;
  font-weight: 600;
  line-height: 49px;
}

p,
ul,
li {
  display: block;
  padding: 0;
  margin: 0;
}

a,
a:hover,
a:active {
  text-decoration: none;
}

a {
  transition: all 0.5s ease;
  font-family: 'Nunito', sans-serif;
}

*::placeholder {
  color: #242424;
}

input,
input:hover,
input:focus,
input:active,
button,
button:hover,
button:focus,
button:active {
  outline: none !important;
  border: none !important;
}

p {
  line-height: 1.5em;
}

.gamburger {
  display: none;
}

.button {
  background: linear-gradient(55.42deg, #9D9792 12.35%, #CECAC5 22.73%, #ECE8E3 30.51%, #EDEAE7 46.07%, #ECE8E3 59.04%, #9D9792 75.91%, #CECAC5 84.98%, #ECE8E3 91.47%, #EDEAE7 105.74%, #ECE8E3 121.3%, #9D9792 142.05%);
  box-shadow: 0px 0px 10px rgba(236, 232, 227, 0.5);
  font-size: 14px;
  text-transform: uppercase;
  color: #242424;
  font-weight: 600;
  letter-spacing: 0.08em;
  max-width: 236px;
  width: 236px;
  height: 44px;
  transition: all .5s ease;
}

.button:hover {
  box-shadow: 0 0 20px rgba(255, 255, 255, 0.9);
}

.close {
  display: none;
}

.modal-form .check__box {
  left: -1.5em;
}

.box-modal {
  text-align: right;
  max-width: 632px;
  width: 632px;
  padding-top: 10px;
  padding-right: 10px;
  border-radius: 10px;
  background-color: #003768;
}

.box-modal .close-button {
  cursor: pointer;
}

.form {
  padding-top: 31px;
  padding-right: 35px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-control {
  background: #E2E2E2;
  width: 236px;
  height: 44px;
  max-width: 236px;
  color: #242424;
  font-size: 16px;
  font-weight: 600;
  padding-left: 20px;
  font-family: Nunito, sans-serif;
}

.form-group {
  position: relative;
}

#loader {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.91);
  overflow: hidden;
  display: none;
}

label.error {
  font-size: 12px;
  color: red;
  position: absolute;
}

#tel-error {
  top: 0;
  top: -25px;
  width: 100%;
  left: 0;
}

input.error {
  border: 1px solid red !important;
}

input.valid {
  border: 1px solid green !important;
}

#loader img {
  position: absolute;
  width: 50px;
  height: 50px;
  left: 50%;
  margin-left: -25px;
  top: 50%;
  margin-top: -25px;
}

#overlay {
  display: none;
  position: fixed;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  left: 0px;
  top: 0px;
  cursor: pointer;
}

#thx {
  background: #373737;
  box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.4);
  text-align: center;
  position: fixed;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 30px;
  color: #fff;
  border-radius: 20px;
  z-index: 999;
}

.author {
  position: relative;
}

.disk {
  position: absolute;
  background: transparent;
  right: -18px;
  bottom: 8vh;
  width: 184px;
  padding: 15% 0;
  border-radius: 50%;
  animation: loader 6s linear infinite;
}

.disk span {
  position: absolute;
  left: 50%;
  width: 0;
  bottom: 50%;
  -ms-transform-origin: 0% 100%;
  -webkit-transform-origin: 0% 100%;
  transform-origin: 0% 100%;
  padding-bottom: 50%;
}

.disk span:nth-of-type(1) {
  transform: rotate(245deg);
}

.disk span:nth-of-type(2) {
  transform: rotate(255deg);
}

.disk span:nth-of-type(3) {
  transform: rotate(265deg);
}

.disk span:nth-of-type(4) {
  transform: rotate(275deg);
}

.disk span:nth-of-type(5) {
  transform: rotate(285deg);
}

.disk span:nth-of-type(6) {
  transform: rotate(295deg);
}

.disk span:nth-of-type(7) {
  transform: rotate(325deg);
}

.disk span:nth-of-type(8) {
  transform: rotate(335deg);
}

.disk span:nth-of-type(9) {
  transform: rotate(345deg);
}

.disk span:nth-of-type(10) {
  transform: rotate(355deg);
}

.disk span:nth-of-type(11) {
  transform: rotate(365deg);
}

.disk span:nth-of-type(12) {
  transform: rotate(375deg);
}

.disk span:nth-of-type(13) {
  transform: rotate(395deg);
}

.disk span:nth-of-type(14) {
  transform: rotate(415deg);
}

.disk span:nth-of-type(15) {
  transform: rotate(425deg);
}

.disk span:nth-of-type(16) {
  transform: rotate(435deg);
}

.disk span:nth-of-type(17) {
  transform: rotate(445deg);
}

.disk span:nth-of-type(18) {
  transform: rotate(455deg);
}

.disk span:nth-of-type(19) {
  transform: rotate(475deg);
}

.disk span:nth-of-type(20) {
  transform: rotate(485deg);
}

.disk span:nth-of-type(21) {
  transform: rotate(495deg);
}

.disk span:nth-of-type(22) {
  transform: rotate(505deg);
}

.disk span:nth-of-type(23) {
  transform: rotate(515deg);
}

.disk span:nth-of-type(24) {
  transform: rotate(525deg);
}

@keyframes loader {
  0% {
    transform: rotate(22deg);
  }
  100% {
    transform: rotate(380deg);
  }
}

.mobile-author {
  position: absolute;
  right: 0;
  display: none;
  top: -84px;
}

.img-mobile {
  display: none;
}

.header {
  background-image: url(../images/header-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.header__wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header .menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 35px;
}

.header .menu__item a {
  font-size: 18px;
  color: #fff;
  font-family: Raleway;
}

.header .menu__item a:hover {
  color: #9E9995;
}

.header .menu__item:not(:last-child) {
  margin-right: 30px;
}

.header__phone {
  margin-top: 35px;
}

.header__phone a {
  color: #fff;
  font-size: 20px;
  letter-spacing: 0.015em;
}

.header__phone a:hover {
  color: #9E9995;
}

.header__phone a img {
  transition: all .5s ease;
}

.header__phone a:hover img {
  transform: scale(1.3);
}

.header__phone a:nth-child(1) {
  margin-right: 5px;
}

.header__phone a:nth-child(2) {
  margin-right: 5px;
}

.header__phone a:nth-child(3) {
  margin-right: 9px;
}

.header .offer {
  padding-top: 30px;
}

.header .offer__title {
  margin-top: 60px;
  letter-spacing: 0.035em;
}

.header .offer__subtitle {
  font-weight: 300;
  letter-spacing: 0.035em;
  font-size: 30px;
  text-transform: uppercase;
  margin-top: 11px;
}

.header .offer__text {
  margin-top: 25px;
}

.header .offer__wrap {
  margin-top: 41px;
  padding-left: 80px;
}

.header .offer__wrap p {
  font-size: 18px;
  letter-spacing: 0.015em;
  max-width: 430px;
  font-weight: 300;
}

.skills {
  padding-top: 125px;
  padding-bottom: 97px;
}

.skills__item {
  display: flex;
  flex-flow: column;
  align-items: center;
  margin-top: 66px;
  min-width: 200px;
}

.skills__item img {
  height: 82px;
}

.skills__item-text {
  margin-top: 23px;
}

.skills__text {
  font-size: 18px;
  line-height: 26px;
}

.skills__text1 {
  margin-top: 81px;
}

.skills__text2 {
  max-width: 700px;
  margin: 18px auto 0;
}

.contacts {
  padding-top: 100px;
  min-height: 555px;
  background-image: url(../images/contacts1-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

.contacts__title {
  max-width: 300px;
  font-weight: 400;
  line-height: 40px;
}

.contacts__text2 {
  margin-top: 27px;
}

.contacts-word {
  position: absolute;
  font-weight: 600;
  font-size: 24px;
}

.contacts-word1 {
  right: 236px;
  top: 20px;
}

.contacts-word2 {
  top: 160px;
  right: 205px;
}

.contacts .form {
  justify-content: flex-start;
}

.contacts .form__btn {
  margin-left: 28px;
}

.reasons {
  padding-top: 106px;
  background-image: url(../images/reasons-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.reasons-wrap {
  margin-top: 50px;
  display: flex;
  justify-content: space-between;
  background: #373737;
  padding: 20px 42px 0 64px;
}

.reasons-wrap__list {
  margin-top: 66px;
}

.reasons-wrap__list li {
  font-size: 18px;
}

.reasons-wrap__list li:not(:last-child) {
  margin-bottom: 30px;
}

.works {
  padding-top: 120px;
  background-image: url(../images/works-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.works__title {
  max-width: 740px;
  margin: 0 auto;
}

.works-wrapper {
  margin-top: 62px;
  display: flex;
}

.works__item {
  position: relative;
  background: #373737;
  padding: 29px 30px 30px 30px;
  min-width: 257px;
}

.works__item:not(:last-child) {
  margin-right: 20px;
}

.works__item img {
  min-height: 80px;
}

.works__item span {
  position: absolute;
  right: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 55px;
  height: 55px;
  font-weight: 600;
  font-size: 24px;
  color: #373737;
  background: linear-gradient(45deg, #9D9792, #CECAC5, #ECE8E3, #EDEAE7, #ECE8E3, #9D9792, #CECAC5, #ECE8E3, #EDEAE7, #ECE8E3, #9D9792);
}

.works__text {
  font-size: 18px;
  margin-top: 24px;
  line-height: 28px;
}

.works-offers {
  margin-top: 97px;
  height: 185px;
  background-repeat: no-repeat;
}

.works-offers img {
  max-width: 100%;
}

.works__subtitle {
  font-size: 24px;
  font-weight: 500;
}

.works__subtitle2 {
  font-size: 14px;
  font-weight: 300;
}

.garanties {
  padding: 78px 0;
}

.garanties__box {
  background: #373737;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 12px 23px;
  margin-top: 36px;
}

.garanties__text {
  font-weight: 500;
  font-size: 24px;
  margin-left: 12px;
}

.cases {
  background-image: url(../images/cases-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.cases__box {
  padding: 34px 50px 33px 35px;
  background: #373737;
  max-width: 970px;
  margin: 28px auto 0;
}

.cases__box-title {
  font-size: 24px;
  text-align: center;
  font-family: Nunito, sans-serif;
}

.cases__box-gallery {
  margin-top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.cases__box-gallery img {
  max-width: 100%;
  height: auto;
}

.cases__box-gallery a:nth-child(2) {
  margin-left: 80px;
}

.contacts2 {
  background-image: url(../images/contacts2-bg.jpg);
  background-repeat: no-repeat;
  background-size: cover;
}

.contacts2 .contacts__text2 {
  font-size: 18px;
}

.contacts2 .contacts-word1 {
  right: unset;
  top: 0;
}

.contacts2 .contacts-word2 {
  right: unset;
  left: 50px;
  top: 140px;
}

.footer {
  padding: 55px 0 32px;
  color: #ededed;
}

.footer-wrap {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #484949;
}

.footer-wrap__text {
  margin-top: 19px;
  font-size: 14px;
  width: 290px;
  max-width: 290px;
}

.footer-wrap__contacts-box {
  text-align: right;
}

.footer-wrap__contacts-box-phone {
  color: #f9f9f9;
  font-size: 14px;
  font-weight: 500;
}

.footer-wrap__contacts-box-mail {
  font-weight: 300;
  font-size: 18px;
  color: #fff;
  margin-bottom: 30px;
  margin-top: 80px;
}

.footer-wrap__contacts-box-mail img {
  margin-right: 15px;
}

.footer-wrap__contacts-box-mail:hover {
  color: #9E9995;
}

.footer__politics {
  text-decoration: underline;
  font-size: 13px;
  font-weight: 300;
  color: #fff;
  margin-top: 13px;
}

.footer .header__phone {
  position: relative;
}

.footer .vk-com {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, 80%);
}

/* ------ Media Queries ------ */
@media screen and (max-width: 1500px) {
  .contacts__text1 {
    margin-top: 90px;
  }
  .contacts-word1 {
    right: 145px;
  }
  .contacts-word2 {
    right: 120px;
  }
}

/*------1201px  -  1366px-------*/
/* 992-1200px */
@media screen and (max-width: 1200px) {
  .header__logo img {
    max-width: 100%;
  }
  .header .menu li {
    margin-right: 20px;
  }
  .header .menu li a {
    font-size: 16px;
  }
  .header__phone a {
    font-size: 17px;
  }
  .header .button {
    margin-top: 20px;
  }
  .works-wrapper {
    margin-top: 32px;
    flex-wrap: wrap;
    justify-content: center;
  }
  .works__item {
    margin-top: 28px;
  }
  .works__item:last-child {
    margin-right: 20px;
  }
  .works-offers {
    margin-top: 47px;
  }
}

/* 768-992px */
@media screen and (max-width: 992px) {
  .header .gamburger {
    display: flex;
    align-items: center;
    font-size: 20px;
    color: #fff;
  }
  .header .gamburger img {
    margin-left: 16px;
  }
  .header__wrap {
    justify-content: space-around;
  }
  .header .menu {
    text-align: center;
    position: absolute;
    width: 100%;
    height: 50%;
    left: 0;
    top: 0;
    z-index: 9;
    background: #242424;
    margin-top: 0;
    display: none;
  }
  .header .menu li {
    margin-right: 0;
    margin-top: 25px;
  }
  .header .menu__item.header__phone a {
    font-family: Nunito, sans-serif;
  }
  .header .open {
    display: block;
    position: absolute;
    right: 20px;
    top: 20px;
    color: #fff;
    opacity: 1;
  }
  .header .close-menu {
    display: none;
  }
  .header .offer__wrap {
    padding-left: 0;
    position: relative;
    padding-bottom: 130px;
  }
  .contacts {
    min-height: 440px;
    background-image: url(../images/contacts-mob-bg.png);
    padding-top: 10px;
  }
  .contacts__title {
    color: #606161;
    text-align: center;
  }
  .contacts-word1 {
    right: unset;
    color: #606161;
  }
  .contacts-word2 {
    right: unset;
    color: #606161;
    left: 50px;
  }
  .contacts__text1 {
    color: #252525;
  }
  .contacts__text2 {
    color: #252525;
  }
  .contacts .form {
    flex-flow: column;
  }
  .contacts .form .button {
    margin-left: 0;
    margin-top: 20px;
  }
  .contacts2 {
    min-height: 500px;
  }
  .contacts2 .contacts-word1 {
    right: unset;
    top: -120px;
  }
  .contacts2 .contacts-word2 {
    right: unset;
    top: 0;
  }
  .reasons {
    padding-top: 0;
  }
  .reasons-wrap {
    flex-flow: column;
    align-items: center;
    padding: 20px 8px 0 64px;
    margin-top: 20px;
  }
  .reasons-wrap img {
    max-width: 100%;
  }
  .cases {
    padding-bottom: 57px;
  }
  .cases__box-gallery {
    margin-top: 0;
    flex-flow: column;
  }
  .cases__box-gallery a {
    margin-top: 20px;
  }
  .cases__box-gallery a img {
    max-width: 100%;
    width: 100%;
  }
  .cases__box-gallery a:nth-child(2) {
    margin-left: 0;
  }
  .footer-wrap {
    flex-wrap: wrap;
  }
  .footer-wrap__contacts-box-mail {
    margin-top: 20px;
    margin-bottom: 0;
  }
  .footer-wrap__menu {
    display: none;
  }
  .footer-rights {
    margin-top: 20px;
  }
  .mobile-author {
    display: block;
  }
}

/* 576-768px */
@media screen and (max-width: 768px) {
  h1 {
    font-size: 36px;
  }
  h2 {
    font-size: 24px;
    line-height: 40px;
  }
  .header .offer__subtitle {
    max-width: 300px;
  }
  .header .offer__wrap {
    padding-bottom: 50px;
  }
  .header .offer__wrap p {
    max-width: 270px;
  }
  .contacts__text1 {
    max-width: 200px;
    margin: 0 auto;
    color: #252525;
    margin-top: 50px;
  }
  .contacts__text2 {
    max-width: 200px;
    margin: 0 auto;
    color: #252525;
  }
  .works__item {
    margin-right: 0 !important;
  }
  .works__item:last-child {
    margin-right: 0 !important;
  }
  .works-offers {
    height: 100%;
    display: flex;
    justify-content: center;
  }
  .works__subtitle {
    margin-top: 60px;
  }
  .works .img-mobile {
    display: block;
  }
  .works .img-desktop {
    display: none;
  }
  .form {
    padding: 10px;
  }
  .form__title {
    font-size: 30px;
  }
  .form__text {
    font-size: 16px;
    width: 100%;
    max-width: 100%;
  }
  .form__btn {
    width: 100%;
  }
}

/* 320-576px */
@media screen and (max-width: 576px) {
  h1 {
    font-size: 35px;
  }
  .header .container {
    padding-right: 0;
  }
  .header .menu {
    height: 60%;
  }
  .header .offer {
    padding-top: 0;
  }
  .header .offer__title {
    margin-top: 0;
  }
  .header .offer__subtitle {
    max-width: 300px;
  }
  .header .offer__text {
    display: none;
  }
  .header .offer__wrap {
    padding-bottom: 69px;
  }
  .header .offer__wrap p {
    max-width: 150px;
  }
  .header__wrap {
    padding-bottom: 50px;
  }
  .header__wrap p {
    max-width: 150px;
    font-size: 16px;
  }
  .header .gamburger img {
    margin-right: 17px;
  }
  .contacts .form {
    align-items: flex-end;
  }
  .contacts__text1 {
    margin-right: 35px;
    text-align: center;
  }
  .contacts__text2 {
    margin-right: 35px;
    text-align: center;
  }
  .mobile-author {
    top: -63px;
  }
  .garanties {
    padding-top: 40px;
    padding-bottom: 50px;
  }
  .garanties__box {
    padding: 12px 5px;
